/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { Grid, Button, makeStyles, Typography } from '@material-ui/core';
import { Color } from 'src/constants';
import { Menu } from 'src/models';
import { Media } from 'src/utils';
import RouteLink from 'components/Utils/RouteLink';

interface Props {
  title: string;
  direction: 'left' | 'right';
  description: string;
  buttonTitle: string;
  menu: Menu;
  photo: string;
}

const MainContentRow = ({ title, direction, description, buttonTitle, menu, photo }: Props) => {
  const style = makeStyles({
    description: {
      lineHeight: 1.8,
      marginBottom: '1rem',
    },
    button: {
      background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
      boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
      color: Color.White,
      textDecoration: 'none',
    },
  })();
  return (
    <Grid
      component="section"
      css={css`
        padding: 3rem 0;
      `}
      container
    >
      <Grid
        component="h2"
        xs={12}
        css={css`
          margin-bottom: 2rem;
        `}
        item
      >
        {title}
      </Grid>
      <Grid container direction={direction === 'left' ? 'row-reverse' : 'row'} spacing={3}>
        <Grid xs={12} md={6} item>
          <Typography dangerouslySetInnerHTML={{ __html: description }} className={style.description} variant="body2" />
          <RouteLink
            to={menu.link}
            css={css`
              text-decoration: none;
            `}
          >
            <Button className={style.button} size="large">
              {buttonTitle}
            </Button>
          </RouteLink>
        </Grid>
        <Grid xs={12} md={6} item>
          <div
            css={css`
              padding-bottom: 50%;
              background-size: cover;
              background-image: url(${photo});
              ${Media.mobile} {
                margin-bottom: 1rem;
              }
            `}
          ></div>
        </Grid>
      </Grid>
    </Grid>
  );
};

export default MainContentRow;
