import { useStaticQuery, graphql } from 'gatsby';

interface ImageQueryResult {
  childImageSharp: {
    fluid: {
      src: string;
    };
  };
}
interface MainImageQueryResult {
  memberImage: ImageQueryResult;
  magazineImage: ImageQueryResult;
  blogImage: ImageQueryResult;
  githubImage: ImageQueryResult;
}
export function useMainImages(): MainImageQueryResult {
  const queryResult = useStaticQuery(graphql`
    query {
      memberImage: file(relativePath: { eq: "main/members.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      magazineImage: file(relativePath: { eq: "main/magazines.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      blogImage: file(relativePath: { eq: "main/blogs.png" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      githubImage: file(relativePath: { eq: "main/github.png" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return queryResult;
}
